
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'AvtivityCon5',
components: {
XHeader
},
data() {
return {
info: {}
};
},
created() {
this.getList();
},
methods: {
async getList() {
this.$vux.loading.show({
text: 'Loading'
});
let res = await this.$http('/queryTaskList');
res.returnList.map(x => {
if (x.taskId == '5') {
this.info = Object.assign({}, x);
console.log(x);
}
});
this.$vux.loading.hide();
},

async getReward() {
this.$vux.loading.show({
text: 'Loading'
});
let res = await this.$http('/getReward', {
body: {
taskId: '5'
}
});
this.$vux.loading.hide();
if (res.returnCode == '0000') {
this.$vux.toast.show({
text: '领取成功',
type: 'success'
});
this.getList();
} else {
this.$vux.toast.show({
text: res.returnMessage,
type: 'warn'
});
}
}
}
};



